.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 75vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.socials {
  padding: 1px;
  margin: 3px;
}


.quote-text {
	font-size: 40px;
	font-family: 'Amatic SC', cursive;
	text-align: center;
	margin: 0 auto;
	width: 80%;
}

.quote-footer{
	margin: 10px 0 10px 0;
	font-size: 20px;
}

.icon {
	margin-top: 10px;
	float: left;
}

.fa-quote-left {
	position: relative;
	top: 34px;
}

.fa-quote-right {
	position: relative;
	bottom: 23px;
	float: right;
}

.fa-twitter {
	font-size: 30px !important;
	color: #80C3F5;
}

.quote-next {
	color: #fff;
	width: 70px;
	font-size: 24px;
	float: right;
	margin-top: 16px;
	padding: 5px 0;
	background-color: transparent;
	border: 1px solid ;
}

.quote-next:focus {
	outline: none;
}

.form {
    position: fixed;
    top: 50%;
    left: 50%;
    height: 30%;
    width: 50%;
    margin: -15% 0 0 -25%;
}

.cardStyle {
    height: 100; 
    width: 100; 
    display: center
}

.profile {
    border: 5px;
    border-color: black;
}

.parent {
    display: flex;
  }
  .narrow {
    width: 200px;
    margin: 5px;
  }
  .wide {
    flex: 1 1;

  }
