@import url(https://fonts.googleapis.com/css?family=Amatic+SC:400,700);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600);




.quote-text {
	font-size: 40px;
	font-family: 'Amatic SC', cursive;
	text-align: center;
	margin: 0 auto;
	width: 80%;
}

.quote-footer{
	margin: 10px 0 10px 0;
	font-size: 20px;
}

.icon {
	margin-top: 10px;
	float: left;
}

.fa-quote-left {
	position: relative;
	top: 34px;
}

.fa-quote-right {
	position: relative;
	bottom: 23px;
	float: right;
}

.fa-twitter {
	font-size: 30px !important;
	color: #80C3F5;
}

.quote-next {
	color: #fff;
	width: 70px;
	font-size: 24px;
	float: right;
	margin-top: 16px;
	padding: 5px 0;
	background-color: transparent;
	border: 1px solid ;
}

.quote-next:focus {
	outline: none;
}
